import { render , html, ViewTemplate , ViewContext } from '@lithium-framework/core';
import '@lithium-framework/router-element';
import 'unofficial-pf-v5-wc';
import 'unofficial-pf-v5-wc-icons';
import '@stephband/slide-show';


let template: ViewTemplate<any> = html`${(context: ViewContext) => {
    let first_name = "";

    if (localStorage.getItem("first_name")) {
        first_name = localStorage.getItem("first_name") || "";
        console.log(first_name);
    }

    return html`<div class='home'>
        <pf-masthead></pf-masthead>
        <section>
            <h3>Plonger dans l'univers implaccable du jiu jitsu Brsilien</h3>
        </section>
        <section>
            <slide-show loop controls="pagination">
                <img src="../images/donkeys.jpg" draggable="false" />
                <img src="../images/tractor.jpg" draggable="false" />
                <img src="../images/mauverin.jpg" draggable="false" />
            </slide-show>
        </section>
    </div>`;
}}`;

document.addEventListener('DOMContentLoaded', () => {
    let isLogged = "";
    const logoutLink = document.getElementById('logout');
    if (localStorage.getItem("isLogged")) {
        isLogged = localStorage.getItem("isLogged") || "";
        const loginLink = document.getElementById('login');
        loginLink?.setAttribute('class', 'hidden')
    }else{
        logoutLink?.setAttribute('class','hidden')
    }

    if (logoutLink) {
        logoutLink.addEventListener('click', (e) => {
            
            e.preventDefault();

            console.log(localStorage)
            localStorage.clear();

            window.location.href = '/';
        });
    }
});




render(template);